import { createContext, useState } from "react";

export const ParcelContext = createContext();

export function ParcelProvider({ children }) {
  const [selectedParcel, setSelectedParcel] = useState(null);

  //  Harita modu (UI: lalama / Hasat / Saym / Salk)
  // Varsaylan "ilac" braktm; MapContainerdaki MODES ile uyumlu.
  const [mapMode, setMapMode] = useState("sayim");

  //  Gruplama
  const [groupMode, setGroupMode] = useState(false);
  const [groupedParcels, setGroupedParcels] = useState([]);

  const toggleGroupParcel = (id) => {
    setGroupedParcels((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]
    );
  };

  const handleGroupMode = (val) => {
    setGroupMode(val);
    if (val) {
      // grup alyorsa tekli seimi temizle
      setSelectedParcel(null);
    } else {
      // grup kapanyorsa listeyi temizle
      setGroupedParcels([]);
    }
  };

  return (
    <ParcelContext.Provider
      value={{
        // seimler
        selectedParcel,
        setSelectedParcel,

        // harita modu
        mapMode,
        setMapMode,

        // gruplama
        groupMode,
        setGroupMode: handleGroupMode,
        groupedParcels,
        setGroupedParcels,
        toggleGroupParcel,
      }}
    >
      {children}
    </ParcelContext.Provider>
  );
}
